<template>
    <div class="info-wrapper each-wrapper">
        <img src="@assets/img/avatar.jpeg" alt="extra_zhao" class="avater-img">
        <div class="nickname">赵益达</div>
        <div class="info">
            <div><p class="count">100</p>
                <p>文章</p></div>
            <div><p class="count">100</p>
                <p>动态</p></div>
            <div><p class="count">100</p>
                <p>分享</p></div>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">

    export default {
        name: '',
        data(){
            return {}
        },
        created() {

        },
        mounted() {

        },
        methods: {},
        components: {}
    }
</script>

<style scoped lang="less">
    .info-wrapper {
        text-align: center;
        .avater-img {
            width: 100px;
            height: 100px;
            border-radius: 50%;
            border:4px solid rgba(210,210,210,0.2);
            transition:all 1s cubic-bezier(.17, .67, .62, 1.22);
            -webkit-transition:all 1s cubic-bezier(.17, .67, .62, 1.22);
            -moz-transition:all 1s cubic-bezier(.17, .67, .62, 1.22);
            -o-transition:all 1s cubic-bezier(.17, .67, .62, 1.22);
            -ms-transition:all 1s cubic-bezier(.17, .67, .62, 1.22);
            &:hover{
                 transform: rotate(360deg);
                 -ms-transform: rotate(360deg);
                 -webkit-transform: rotate(360deg);
             }
        }
        .nickname {
            margin: 10px auto;
            font-size: 18px;
        }
        .info {
            display: flex;
            div {
                flex: 1;
                text-align: center;
                p {
                    font-size: 12px;
                    &.count {
                        font-size: 18px;
                        /*font-weight: bold;*/
                    }
                }
            }
        }
    }
</style>



