<template>
    <div class="page-header">
        <div class="header-wrapper">
        	<div class="name">
        		<!--<img src="../../assets/img/avatar.jpeg"/>-->
        		赵益达--前端初学者
        	</div>
        	<div class="class">
        		<ul>
        			<li :class='[($route.path == "/" || $route.path == "/index")?"active":""]' @click='$router.push({name:"index",params:{name:"123"}})'>首页</li>
        			<li :class='[$route.path == "/archive"?"active":""]' @click='$router.push({path:"/archive"})'>归档</li>
        			<li :class='[$route.path == "/dynamic"?"active":""]' @click='$router.push({path:"/dynamic"})'>动态</li>
        			<li :class='[$route.path == "/share"?"active":""]' @click='$router.push({path:"/share"})'>分享</li>
        			<li :class='[$route.path == "/about"?"active":""]' @click='$router.push({path:"/about"})'>关于</li>
        		</ul>
        	</div>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">

    export default {
        name: '',
        data(){
            return {}
        },
        created() {

        },
        mounted() {

        },
        methods: {},
        components: {}
    }
</script>

<style scoped lang="less">

</style>



