<template>
    <div class="comment-item">
        <img src="@assets/img/sucai/1.jpg" class="avatar" alt="">
        <div class="name">不愿透露姓名的吴彦祖先生</div>
        <div class="comments-context">博主很帅，我以吴彦祖的颜值发誓。</div>
        <div class="comments-time">2019-12-31 (Windows 7 x64 Edition / Google Chrome 76.0.3809.132)</div>
    </div>
</template>

<script type="text/ecmascript-6">

    export default {
        name: '',
        data(){
            return {}
        },
        created() {

        },
        mounted() {

        },
        methods: {},
        components: {}
    }
</script>

<style scoped lang="less">
    .comment-item{
        font-size: 14px;
        overflow: hidden;
        margin-bottom: 30px;
    .avatar{
        width: 60px;
        height: 60px;
        border-radius:50%;
        border:4px solid rgba(210,210,210,0.2);
        float: left;
    }
    .name{
        font-weight: bold;
        padding-left: 70px;
        color: #666;
        padding-top: 4px;
        margin-bottom: 5px;
    }
    .comments-context{
        padding-left: 70px;
        color:#999;
    }
    .comments-time{
        font-size: 12px;
        padding-left: 70px;
        color:#999;
        margin-top: 3px;
    }
    }
</style>



